"use client";
import Image from "next/image";
import NameIntro from "../intro";
import { useState } from "react";
import { motion, Variants } from "framer-motion";

const magicWordsContainerVariants: Variants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.06,
    },
  },
};
const magicWordsVariants: Variants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
  },
};
const magicWords: string[] = [
  "Hi,I'm kismat",
  "I create websites and deliver",
  "ios apps",
];

const Hero = () => {
  const [intro, setIntro] = useState(true);

  //for magicCharacters

  // if (intro) return <NameIntro intro={intro} setIntro={setIntro} />;
  return (
    <section className="h-screen overflow-x-hidden flex flex-col">
      <div className="flex  justify-center mt-24">
        <Image
          className="rounded-full"
          src={"/profilepicture.jpg"}
          alt="KK"
          width={150}
          height={150}
          priority
        />
      </div>
      <motion.div
        variants={magicWordsContainerVariants}
        initial="hidden"
        animate="visible"
        className="font-semibold text-xl"
      >
        {magicWords[0].split("").map((c, i) => (
          <motion.span variants={magicWordsVariants} key={i}>
            {c}
          </motion.span>
        ))}
        <br />
        {magicWords[1].split("").map((c, i) => (
          <motion.span variants={magicWordsVariants} key={i}>
            {c}
          </motion.span>
        ))}
        <br />
        {magicWords[2].split("").map((c, i) => (
          <motion.span variants={magicWordsVariants} key={i}>
            {c}
          </motion.span>
        ))}
      </motion.div>
      <div></div>
      <div></div>
      <div></div>
    </section>
  );
};
export default Hero;
